import React, { useState } from "react";

const colorSets = [
  {
    name: "Set 1",
    class: "set1",
    colors: ["#fefae0", "#ffffff", "#283618", "#606c38", "#283618", "#bc6c25", "#dda15e" ]
    
  },
  {
    name: "Set 2",
    class: "set2",
    colors: ["#edf2f4", "#ffffff", "#2b2d42", "#ef233c", "#d90429", "#8d99ae", "#d9d9d9"]
  },
  {
    name: "Set 3",
    class: "set3",
    colors: ["#ffffff", "#ffffff", "#353535", "#284b63", "#184e77", "#3c6e71", "#1a759f"]
  },
];

function ColorPicker(props) {
  const [selectedClass, setSelectedClass] = useState("");

  const handleClassChange = (event) => {
    setSelectedClass(event.target.value);
    document.body.className = "";
    document.body.classList.add(event.target.value);
    props.handleChangeData(event);
  };

  return (
    <>
      <h2>Select a color set:</h2>
      <div className="color-set">
        {colorSets.map((colorSet, index) => (
          <div key={index}>
            <input
              type="radio"
              name="colorSet"
              id={colorSet.class}
              value={colorSet.class}
              checked={selectedClass === colorSet.class}
              onChange={handleClassChange}
            />
            <label htmlFor={colorSet.class}>{colorSet.name}
              <div className="color-preview">
                {colorSet.colors.map((color, index) => {
                  return <div key={index} className="color-preview-item" style={{ backgroundColor: color }}></div>;
                })}
              </div>
              
            </label>
          </div>
        ))}
      </div>
    </>
  );
}

export default ColorPicker;