import React, { useState } from 'react';
import { Form, FloatingLabel, Row, Col } from 'react-bootstrap';

function TitleSite(props){
  const [title, setTitle] = useState('');
  const [logoUrl, setLogoUrl] = useState('');
  const [desc, setDesc] = useState('');

  const handleTitleChange = (event) => {
    setTitle(event.target.value);
    props.handleChangeData(event);
  };

  const handleLogoUrlChange = (event) => {
    setLogoUrl(event.target.value);
    props.handleChangeData(event);
  };

  const handleDescChange = (event) => {
    setDesc(event.target.value);
    props.handleChangeData(event);
  };

  return(
    <Row>
      <Col md>
        <FloatingLabel controlId="floatingWebTitle" label="Назва сайту" className="mb-3">
          <Form.Control type="text" name='siteName' value={title} onChange={handleTitleChange} placeholder="Назва сайту" />
        </FloatingLabel>
      </Col>
      <Col md>
       <FloatingLabel controlId="floatingWebLogo" label="Посилання на логотип" className="mb-3">
          <Form.Control type="text" name='siteLogo' value={logoUrl} onChange={handleLogoUrlChange} placeholder="Посилання на логотип" />
        </FloatingLabel>
      </Col>
      <Col md={12}>
       <FloatingLabel controlId="floatingWebDesc" label="Опис сайту" className="mb-3">
          <Form.Control as="textarea" name='siteDesc' value={desc} onChange={handleDescChange}  style={{ height: '100px' }} placeholder="Опис сайту" />
        </FloatingLabel>
      </Col>
    </Row>
  )
}

export default TitleSite;