import React from "react";
import 'react-quill/dist/quill.snow.css';
import {Row, Col, Container} from 'react-bootstrap';

function FooterComponent({ footer, siteData }) {
  if (!footer) {
    return null;
  }

  return(
    <footer className={`layout layout__${footer.padding} ${(footer.type === "bg") ? `layout__bg layout__bg__footer layout__bg__footer_${footer.variant}`: ""}`}>
      <Container fluid={footer && footer.container === "full" && true}>
        <Row>
          <Col md={6} lg>
            {(siteData.siteName !== null && footer.logo === "false") ? (<h3>{siteData.siteName}</h3>) : (siteData.siteLogo !== null && footer.logo === "true") ? (<img src={`${siteData.siteLogo}`} alt={`${siteData.siteName}`} className="navbarImg" />) : (<h3>{siteData.siteName}</h3>) }
          </Col>
          <Col md={6} lg>
          {footer.links.map((item, index) => (
            <div className="text-center" key={index}>
              <a href={item.href}>
                {item.label}
              </a>
            </div>
          ))}
          </Col>
          <Col md={6} lg>
            {footer.social.map((item, index) => (
              <div className="text-center text-lg-end" key={index}>
                <a href={item.href}>
                  {item.name}
                </a>
              </div>
            ))}
          </Col>
          <Col xs={12} className="mt-5">
            Всі права захищені © {new Date().getFullYear()} {siteData.siteName}
          </Col>
        </Row>
      </Container>
    </footer>
  )
}

export default FooterComponent;