import React, { useState, useEffect } from 'react';
import FooterComponent from '../../components/Footer/footer';
import MultiStepComponent from "../../components/FormGenerator";
import Navigation from "../../components/Navbar/navbar";
import SectionComponent from '../../components/Section/section';

let flag = false;

function App() {
  const [formData, setFormData] = useState({});

  useEffect(() => {
    if(localStorage.hasOwnProperty("myData") && !flag){
      const myData = JSON.parse(localStorage.getItem('myData'))
      setFormData(() => ({
        ...myData 
      }));
      flag = true
    }
  }, []);

  document.body.className = "";
  document.body.classList.add(formData.colorSet);
  // console.log(formData)
  
  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value,
    }));
  };

  const handleChangObject = (name, value) => {
    setFormData(prevState => ({
      ...prevState,
      [name]: value,
    }));
  };

  setTimeout(()=>{
    localStorage.setItem("myData", JSON.stringify(formData))
  }, 500)

  return (
    <>
      <div className="form">
        <MultiStepComponent siteData={formData} handleChangeData={handleChange} handleChange={handleChangObject}></MultiStepComponent>
      </div>
      <div className="site">
        <Navigation navbar={formData.navbar} siteData={formData} handleChange={handleChangObject} />
        <SectionComponent sections={formData.sections} />
        <FooterComponent siteData={formData} footer={formData.footer}/>
      </div>
    </>
  );
}

export default App;
