import React, { useState,  useEffect } from "react";
import { Form, Row, Col, FloatingLabel } from 'react-bootstrap';

function FooterGenerator ({handleChange}) {
  const [footer, setFooter] = useState({links: [], social: [], padding: 'sm', type: "bg", variant: 1});

  useEffect(() => {
    let data = JSON.parse(localStorage.getItem('myData'));
    const footerSections = data.footer;
    if (footerSections) {
      setFooter(footerSections)
    }
  }, []);

  useEffect(() => {
    handleChange("footer", footer)
  }, [footer]);

  const handleFieldChange = (event, field, value) => {
    footer[field] = value !== undefined ? value : event.target.value;
    setFooter(footer);
    handleChange("footer", footer)
  };

  const handleAddLink = () => {
    const newLink = { label: '', href: '' };
    footer.links.push(newLink)
    setFooter(footer);
    handleChange("footer", footer)
  };

  const handleRemoveLink = (index) => {
    footer.links.splice(index, 1);
    setFooter(footer);
    handleChange("footer", footer)
  };

  const handleLinkChange = (event, index) => {
    const { name, value } = event.target;
    footer.links[index][name] = value;
    setFooter(footer);
    handleChange("footer", footer)
  };

  const handleAddSocial = () => {
    const newSocial = { name: '', href: '' };
    footer.social.push(newSocial)
    setFooter(footer);
    handleChange("footer", footer)
  };

  const handleRemoveSocial = (index) => {
    footer.social.splice(index, 1);
    setFooter(footer);
    handleChange("footer", footer)
  };

  const handleSocialChange = (event, index) => {
    const { name, value } = event.target;
    footer.social[index][name] = value;
    setFooter(footer);
    handleChange("footer", footer)
  };

  return (
    <>
    <Form>
      <Form.Group controlId="navigationOptions">
        <Form.Label>Налаштуйте підвал сайту</Form.Label>
        <Row>
          <Col md={3}>
            <FloatingLabel controlId="floatingSelectContainer" label="Тип контейенра" className="mb-3">
              <Form.Select value={footer.container} onChange={(event) => handleFieldChange(event, 'container')} aria-label="Floating label select example">
                  <option value="default">З відступами</option>
                  <option value="full">На всю ширину</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          <Col md={3}>
            <FloatingLabel controlId="floatingSelectType" label="Вигляд підвалу" className="mb-3">
              <Form.Select value={footer.type} onChange={(event) => handleFieldChange(event, 'type')} aria-label="Floating label select example">
                  <option value="bg">З фоновим кольором</option>
                  <option value="noBg">Без фону</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          { footer.type === "bg" && (
            <Col md={3}>
            <FloatingLabel controlId="floatingSelectType" label="Варіант фону" className="mb-3">
              <Form.Select value={footer.variant} onChange={(event) => handleFieldChange(event, 'variant')} aria-label="Floating label select example">
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          )
          }
          <Col md={3}>
            <FloatingLabel controlId="floatingSelectAlign" label="Логотип у вигляді" className="mb-3">
              <Form.Select value={footer.logo} onChange={(event) => handleFieldChange(event, 'logo')} aria-label="Floating label select example">
                  <option value="false">Тексту</option>  
                  <option value="true">Картинки</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          <Col md={6}>
            <FloatingLabel controlId="floatingSelectAlign" label="Вирівнювання пунктів меню" className="mb-3">
              <Form.Select value={footer.align} onChange={(event) => handleFieldChange(event, 'align')} aria-label="Floating label select example">
                  <option value="left">З ліва</option>  
                  <option value="right">З права</option>
                  <option value="center">По центру</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          <Col md={6}>
            <FloatingLabel controlId="floatingSelectContainer" label="Відступи" className="mb-3">
              <Form.Select value={footer.padding} onChange={(event) => handleFieldChange(event, 'padding')}>
              <option value="sm">Маленькі</option>
              <option value="md">Середні</option>
              <option value="lg">Великі</option>
              <option value="xl">Дуже великі</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
        </Row>
      </Form.Group>
    </Form>
    {footer.links.map((link, index) => (
      <Row className="g-2 mb-3" key={index}>
        <Col md>
          <Form.Control
                    type="text"
                    name="label"
                    placeholder="Назва посилання"
                    value={link.label}
                    onChange={(event) => handleLinkChange(event, index)}
          />
        </Col>
        <Col md>
        <Form.Control
                    type="text"
                    name="href"
                    placeholder="Посилання"
                    value={link.href}
                    onChange={(event) => handleLinkChange(event, index)}
        />
        </Col>
        <Col md>
          <button className="btn btn-outline-danger" onClick={() => handleRemoveLink(index)}><i className="bi bi-trash"></i></button>
        </Col>
      </Row>
    ))}
    <Row className="g-2 mb-4">
          <Col md="auto">
            <button className="btn btn-outline-info" onClick={() => handleAddLink()}>
              Додати посилання
            </button>
          </Col>
    </Row>
    {footer.social.map((item, index) => (
      <Row className="g-2 mb-3" key={index}>
        <Col md>
          <Form.Control
            type="text"
            name="name"
            placeholder="Назва соц.мережі"
            value={item.name}
            onChange={(event) => handleSocialChange(event, index)}
          />
        </Col>
        <Col md>
          <Form.Control
            type="text"
            name="href"
            placeholder="Посилання на соц.мережу"
            value={item.href}
            onChange={(event) => handleSocialChange(event, index)}
          />
        </Col>
        <Col md>
         <button className="btn btn-outline-danger" onClick={() => handleRemoveSocial(index)}><i className="bi bi-trash"></i></button>
        </Col>
      </Row>
    ))}
    <Row className="g-2 mb-4">
          <Col md="auto">
            <button className="btn btn-outline-info" onClick={() => handleAddSocial()}>
              Додати соц.мережу
            </button>
          </Col>
    </Row>
    </>
  );
};

export default FooterGenerator;