import React from "react";
import 'react-quill/dist/quill.snow.css';
import {Row, Col} from 'react-bootstrap';
import AccordionBlock from "../Accordion";

function SectionComponent({ sections }) {
  if (!sections) {
    return null;
  }

  return (
    sections.map((section, index) => {
      return (
        <section className={`layout layout__${section.padding} ${(section.type === "textBg" || section.type === "imageBg") ? "layout__bg": ""} ${section.background ? 'layout__img' : ''}`} id={section.id} key={index}>
          <div className={ section.container === "full" ? "container-fluid" : "container"}>
            {section.type === 'faq' ? 
              (
                <>
                  <h3 className="mb-4">FAQ</h3>
                  <AccordionBlock items={section.faq}/>
                </>
              )
          :(
            <>
            { section.row === "true" ? (
              <>
                <Row className={`align-items-${section.rowAlign} justify-content-${section.rowJustify}`}>
                  <Col lg={section.rowLeft} className={`order-${section.orderTextMB} order-lg-${section.orderTextPC}`}>
                    <div dangerouslySetInnerHTML={{__html: section.content}}></div>
                  </Col>
                  <Col lg={section.rowRigth} className="text-center">
                    {(section.type === "image" || section.type === "imageBg") &&
                      <>
                        <img src={section.image} alt={section.title}/>
                      </>
                    }
                  </Col>
                </Row>
                </>
              ) : (
                <>
                  <div dangerouslySetInnerHTML={{__html: section.content}}></div>
                  {(section.type === "image" || section.type === "imageBg") &&
                    <>
                      <img src={section.image} alt={section.title}/>
                    </>
                  }
                </>
              )}
              </>
            ) 
          }
             {section.type === "text" && section.background &&
                  <img src={section.image} alt={section.title}/>
              }
          </div>
        </section>
      )
    })
  );
}

export default SectionComponent;