import React, { useState, useEffect } from 'react';
import { Form, Row, Col, FloatingLabel } from 'react-bootstrap';

function NavigationChooser({handleChangeData}) {
  const [selectedOption, setSelectedOption] = useState({variant: 1, links: [], logo: "false"});

  useEffect(() => {
    let data = JSON.parse(localStorage.getItem('myData'));
    const storedNavbar = data.navbar;
    if (storedNavbar) {
      setSelectedOption(storedNavbar);
    }
  }, []);

  const handleFieldChange = (event, field, value) => {
    selectedOption[field] = value !== undefined ? value : event.target.value;
    setSelectedOption(selectedOption);
    handleChangeData("navbar", selectedOption)
  };

  const handleAddLink = () => {
    const newLink = { title: '', href: '' };
    selectedOption.links.push(newLink)
    setSelectedOption(selectedOption);
    handleChangeData("navbar", selectedOption)
  };

  const handleRemoveLink = (index) => {
    selectedOption.links.splice(index, 1);
    setSelectedOption(selectedOption);
    handleChangeData("navbar", selectedOption)
  };

  const handleLinkChange = (event, index) => {
    const { name, value } = event.target;
    selectedOption.links[index][name] = value;
    setSelectedOption(selectedOption);
    handleChangeData("navbar", selectedOption)
  };

  // useEffect(() => {
  //   handleChangeData("navbar", selectedOption)
  // }, [handleChangeData, selectedOption]);

  return (
    <Form>
      <Form.Group controlId="navigationOptions">
        <Form.Label>Налаштуйте навігаційну панель</Form.Label>
        <Row>
          <Col md={3}>
            <FloatingLabel controlId="floatingSelectContainer" label="Тип контейенра" className="mb-3">
              <Form.Select value={selectedOption.container} onChange={(event) => handleFieldChange(event, 'container')} aria-label="Floating label select example">
                  <option value="default">З відступами</option>
                  <option value="full">На всю ширину</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          <Col md={3}>
            <FloatingLabel controlId="floatingSelectType" label="Адаптація навігаційної панелі" className="mb-3">
              <Form.Select value={selectedOption.type} onChange={(event) => handleFieldChange(event, 'type')} aria-label="Floating label select example">
                  <option value="collapse">Згортання</option>
                  <option value="offcanvas">Правий сайдбар</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          <Col md={3}>
            <FloatingLabel controlId="floatingSelectType" label="Варіант фону" className="mb-3">
              <Form.Select value={selectedOption.variant} onChange={(event) => handleFieldChange(event, 'variant')} aria-label="Floating label select example">
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                  <option value="4">4</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          <Col md={3}>
            <FloatingLabel controlId="floatingSelectAlign" label="Логотип у вигляді" className="mb-3">
              <Form.Select value={selectedOption.logo} onChange={(event) => handleFieldChange(event, 'logo')} aria-label="Floating label select example">
                  <option value="false">Тексту</option>  
                  <option value="true">Картинки</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          <Col md={6}>
            <FloatingLabel controlId="floatingSelectAlign" label="Вирівнювання пунктів меню" className="mb-3">
              <Form.Select value={selectedOption.align} onChange={(event) => handleFieldChange(event, 'align')} aria-label="Floating label select example">
                  <option value="left">З ліва</option>  
                  <option value="right">З права</option>
                  <option value="center">По центру</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
          <Col md={6}>
            <FloatingLabel controlId="floatingSelectAlign" label="Закріпити навбар" className="mb-3">
              <Form.Select value={selectedOption.fixed} onChange={(event) => handleFieldChange(event, 'fixed')} aria-label="Floating label select example">
                  <option value="true">Так</option>  
                  <option value="false">Ні</option>
              </Form.Select>
            </FloatingLabel>
          </Col>
        </Row>
        {selectedOption.links.map((link, index) => (
          <Row className="g-2 mb-3" key={index}>
            <Col md>
              <Form.Control
                        type="text"
                        name="title"
                        placeholder="Назва посилання"
                        value={link.title}
                        onChange={(event) => handleLinkChange(event, index)}
              />
            </Col>
            <Col md>
            <Form.Control
                        type="text"
                        name="href"
                        placeholder="Посилання"
                        value={link.href}
                        onChange={(event) => handleLinkChange(event, index)}
            />
            </Col>
            <Col md>
              <button type='button' className="btn btn-outline-danger" onClick={() => handleRemoveLink(index)}><i className="bi bi-trash"></i></button>
            </Col>
          </Row>
        ))}
        <Row className="g-2 mb-4">
          <Col md="auto">
            <button type='button' className="btn btn-outline-info" onClick={() => handleAddLink()}>
              Додати посилання
            </button>
          </Col>
         </Row>
      </Form.Group>
    </Form>
  );
}

export default NavigationChooser;