import { useState } from 'react';

import ColorPicker from "../ColorPicker";
import NavigationChooser from "../../components/Navbar/";
import TitleSite from "./sitename";
import SectionGenerator from "../Section";

import { Row, Col, Container  } from 'react-bootstrap';
import FooterGenerator from '../Footer';

const StepOne = ({ onNext, changeData }) => {

  const handleNext = () => {
    onNext();
  };

  return (
    <Container>
      <Row>
        <Col>
        <h2>Крок 1</h2>
        <p>Налаштуйте кольори та інформацію про сайт</p>
         <TitleSite handleChangeData={changeData}></TitleSite>
        </Col>
        <Col>
          <ColorPicker handleChangeData={changeData}></ColorPicker>
        </Col>
      </Row>
      <button className='btn btn-warning mb-3' onClick={handleNext}>Next</button>
    </Container>
  );
};

const StepTwo = ({ onNext, changeData }) => {
  const handleNext = () => {
    onNext();
  };

  return (
    <Container>
      <h2>Крок 2</h2>
      <NavigationChooser handleChangeData={changeData}></NavigationChooser>
      <Row>
        <Col>
         
        </Col>
        <Col>
          
        </Col>
      </Row>
      <button className='btn btn-warning mb-3' onClick={handleNext}>Next</button>
    </Container>
  );
};

const StepThree = ({ onNext, changeData }) => {
  const handleNext = () => {
    onNext();
  };

  return (
    <Container>
      <h2>Крок 3</h2>
      <p>Налаштуйте секції сайту</p>
      <SectionGenerator handleChange={changeData}/>
      <button className='btn btn-warning mb-3' onClick={handleNext}>Next</button>
    </Container>
  );
};

const StepFour = ({ onNext, changeData }) => {
  const handleNext = () => {
    onNext();
  };

  return (
    <Container>
      <h2>Крок 4</h2>
      <FooterGenerator handleChange={changeData}></FooterGenerator>
      <button className='btn btn-warning mb-3' onClick={handleNext}>Next</button>
    </Container>
  );
};

const MultiStepComponent = ({handleChange, handleChangeData, siteData}) => {
  const [step, setStep] = useState(1);

  const handleNextStep = () => {
    setStep(step + 1);
  };

  switch (step) {
    case 1:
      return <StepOne siteData={siteData} onNext={handleNextStep} changeData={handleChangeData} />;
    case 2:
      return <StepTwo siteData={siteData} onNext={handleNextStep} changeData={handleChange} />;
    case 3:
      return <StepThree siteData={siteData} onNext={handleNextStep} changeData={handleChange}/>;
    case 4:
      return <StepFour siteData={siteData} onNext={handleNextStep} changeData={handleChange}/>;
    default:
      return null;
  }
};

export default MultiStepComponent;