import React from 'react';
import { Navbar, Nav, Container, Offcanvas  } from 'react-bootstrap';

function Navigation(props) {
  const { navbar, siteData } = props;

  return (
    <>
    <Navbar className={`navbar_bg_${navbar && navbar.variant}`} sticky={navbar && navbar.fixed === "false" ? "false": "top"} expand="lg">
      <Container fluid={navbar && navbar.container === "full" && true}>
        <Navbar.Brand href="#home">{(navbar && siteData.siteName !== null && navbar.logo === "false") ? siteData.siteName : (navbar && siteData.siteLogo !== null && navbar.logo === "true") ? (<img src={`${siteData.siteLogo}`} alt={`${siteData.siteName}`} className="navbarImg" />) : siteData.siteName }</Navbar.Brand>
        {navbar && navbar.type === "collapse" && (
          <>
            <Navbar.Toggle aria-controls="basic-navbar-nav">
              <span className="icon-bar top-bar"></span>
              <span className="icon-bar middle-bar"></span>
              <span className="icon-bar bottom-bar"></span>
            </Navbar.Toggle>
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className={navbar && navbar.align === "left" ? "me-auto": navbar.align === "right" ? "ms-auto" : navbar.align === "center" ? "mx-auto": ""}>
                { navbar.links !== undefined && 
                    navbar.links.map((item, index) => (
                      <Nav.Item key={index}>
                        <Nav.Link href={item.href}>
                          {item.title}
                        </Nav.Link>
                      </Nav.Item>
                    ))
                }
              </Nav>
            </Navbar.Collapse>
          </>
        )}
         {navbar && navbar.type === "offcanvas" && (
          <>
            <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-lg`}>
              <span className="icon-bar top-bar"></span>
              <span className="icon-bar middle-bar"></span>
              <span className="icon-bar bottom-bar"></span>
            </Navbar.Toggle>
            <Navbar.Offcanvas
              id={`offcanvasNavbar-expand-xxl`}
              aria-labelledby={`offcanvasNavbarLabel-expand-xxl`}
              placement="end"
            >
              <Offcanvas.Header closeButton>
                <Offcanvas.Title id={`offcanvasNavbarLabel-expand-xxl`}>
                  {siteData.siteName !== null ? siteData.siteName : "Type your name"}
                </Offcanvas.Title>
              </Offcanvas.Header>
              <Offcanvas.Body>
                <Nav className={`flex-grow-1 pe-3 ${navbar && navbar.align === "left" ? "justify-content-start ": navbar.align === "right" ? "justify-content-end" : navbar.align === "center" ? "justify-content-center": ""}`}>
                  { navbar.links !== undefined && 
                    navbar.links.map((item, index) => (
                      <Nav.Item key={index}>
                        <Nav.Link href={item.href}>
                          {item.title}
                        </Nav.Link>
                      </Nav.Item>
                    ))
                  }
                </Nav>
              </Offcanvas.Body>
            </Navbar.Offcanvas>
          </>
        )}
      </Container>
    </Navbar>
    </>
  )
}

export default Navigation;